import styled from '@emotion/styled';
import { Input } from 'antd';
import React, { FC, memo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { E_WITHDRAWAL_METHODS, IWithdrawalMethod } from '../../interfaces/components/withdrawal-method.interface';

const Subtitle = styled.span`
  margin-top: 16px;
  color: var(--333333-personal-area-affiliate-program-withdrawal-form);
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 9px;
`;

interface IPayPalForm {
  onValidated: (isValid: boolean, method: IWithdrawalMethod) => any;
}

const PayPalForm: FC<IPayPalForm> = (props) => {
  const [email, setEmail] = useState<string>('');

  const { t: translation } = useTranslation();

  const onEmailChange = (e: any) => {
    const { value } = e.target;

    const EMAIL_REGEX =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    const isEmailValid = EMAIL_REGEX.test(value);
    setEmail(value);
    props.onValidated(isEmailValid, {
      type: E_WITHDRAWAL_METHODS.paypal,
      currency: 'USD',
      paypalEmail: value,
    });
  };

  return (
    <>
      <Subtitle>{translation('base.yourEmail')}</Subtitle>
      <Input type='email' value={email} placeholder={translation('base.yourEmail')} onChange={onEmailChange} />
    </>
  );
};

export default memo(PayPalForm);
