export enum E_WITHDRAWAL_METHODS {
  'paypal' = 'paypal',
  'bank' = 'bank',
  'card-number' = 'card-number',
  'usdt' = 'usdt',
}

export const WITHDRAWAL_METHODS = Object.values(E_WITHDRAWAL_METHODS);
export type T_WITHDRAWAL_METHODS = (typeof WITHDRAWAL_METHODS)[number];

export interface IWithdrawalMethod {
  type: T_WITHDRAWAL_METHODS;
  currency?: 'EUR'|'USD';
  bankName?: string;
  bankUsername?: string;
  bankAccountNumber?: string;
  bankRoutingNumber?: string;
  bankAccountType?: string;

  paypalEmail?: string;
  usdtWalletAddress?: string;

  cardNumber?: string;
  phoneNumber?: string;
  cardHolderName?: string;
}
