import styled from '@emotion/styled';
import { Icon, message } from 'antd';
import React, { FC, memo, useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { useTranslation } from 'react-i18next';

import IconPencil from '../../../../../ui/icons/IconPencil';

const IconContainer = styled.div`
  margin-left: 6px;
  display: inline-block;
  cursor: pointer;
  user-select: none;
`;

const EmptyNoteText = styled.span`
  color: var(--7E7E7E-personal-area-affiliate-program-table-row);
  opacity: 0.8;
  border-bottom: 1px dashed var(--7E7E7E-personal-area-affiliate-program-table-row);
  cursor: pointer;
`;

const TdRightHanded = styled.td`
  text-align: right;
`;

interface ITableRow {
  rowInfo: any;
  index: number;
  onNoteChange?: (index: number, note: string) => any;
}

const TableRow: FC<ITableRow> = (props) => {
  const [noteEditable, changeNoteEditable] = useState<boolean>(false);
  const [note, changeNote] = useState<string>(props.rowInfo.notes);

  const { t: translation } = useTranslation();

  const link=`go.gologin.com/${props.rowInfo.uri}`;

  return (
    <>
      <tr>
        <td>{props.index + 1}</td>
        <td style={{ display: 'flex' }}>
          <span>
            {link.length > 50 ? link.slice(0, 50) + '...' : link}
          </span>
          <CopyToClipboard
            text={`https://go.gologin.com/${props.rowInfo.uri}`}
            onCopy={() => message.success(translation('base.copiedText'))}
          >
            <IconContainer>
              <Icon type='copy'/>
            </IconContainer>
          </CopyToClipboard>
        </td>
        {/*<td>*/}
        {/*  {*/}
        {/*    noteEditable*/}
        {/*    ? (*/}
        {/*      <>*/}
        {/*        editable...*/}
        {/*      </>*/}
        {/*    )*/}
        {/*    : (*/}
        {/*      note*/}
        {/*      ? (*/}
        {/*        <>*/}
        {/*          {note}*/}
        {/*          <IconContainer>*/}
        {/*            <IconPencil onClick={() => changeNoteEditable(true)} />*/}
        {/*          </IconContainer>*/}
        {/*        </>*/}
        {/*      )*/}
        {/*      : (*/}
        {/*        <>*/}
        {/*          <EmptyNoteText onClick={() => changeNoteEditable(true)}>*/}
        {/*            Add notes*/}
        {/*          </EmptyNoteText>*/}
        {/*          <IconContainer>*/}
        {/*            <IconPencil onClick={() => changeNoteEditable(true)} />*/}
        {/*          </IconContainer>*/}
        {/*        </>*/}
        {/*      )*/}
        {/*    )*/}
        {/*  }*/}
        {/*</td>*/}
        <TdRightHanded>{props.rowInfo.clicks}</TdRightHanded>
        <TdRightHanded>{props.rowInfo.referrals}</TdRightHanded>
        <TdRightHanded>{props.rowInfo.paidUsersCount}</TdRightHanded>
        <TdRightHanded>${props.rowInfo.sumAmount}</TdRightHanded>
        {/*<td>{Intl.NumberFormat('en-US', {*/}
        {/*  style: 'currency',*/}
        {/*  currency: 'USD',*/}
        {/*  minimumFractionDigits: 0,*/}
        {/*  maximumFractionDigits: 0,*/}
        {/*}).format(props.rowInfo.totalInCents / 100 | 0)}</td>*/}
      </tr>
    </>
  );
};

export default memo(TableRow);
