import styled from '@emotion/styled';
import React, { FC, memo } from 'react';
import { useTranslation } from 'react-i18next';

import IconArrowLeftPayment from '../../../../ui/icons/IconArrowLeftPayment';
import { createWithdrawal } from '../api';
import { AffiliateComponent } from '../index';
import { ICreateWithdrawal } from '../interfaces';
import Form from './components/Form';

const PageTitle = styled('span')`
  margin-left: 16px;
  color: var(--000000-personal-area-affiliate-program-withdrawal-form);
  font-family: Roboto;
  font-size: 20px;
  line-height: 23px;
`;

const Col = styled('div')`
  display: flex;
  flex-direction: column;
  margin-top: 20px;
`;

const Row = styled('div')`
  display: flex;
  cursor: pointer;
  align-items: center;
  margin-bottom: 31px;
`;

interface IWithdrawalForm {
  changeComponent: (component: AffiliateComponent) => any;
  availableBalance: number;
}

const WithdrawalForm: FC<IWithdrawalForm> = (props) => {
  const { t: translation } = useTranslation();

  const createWithdrawalRequest = async (withdrawal: ICreateWithdrawal) => {
    try {
      await createWithdrawal(withdrawal);
      props.changeComponent('payment-history');
    } finally {
      return null;
    }
  };

  return (
    <>
      <Col>
        <Row onClick={() => props.changeComponent('overview')}>
          <IconArrowLeftPayment />
          <PageTitle>{translation('personalArea.affiliateProgram.withdraw.funds')}</PageTitle>
        </Row>
        <Form
          maxWithdrawSumInCents={props.availableBalance}
          onWithdrawClicked={createWithdrawalRequest}
        />
      </Col>
    </>
  );
};

export default memo(WithdrawalForm);
