import React, { FC, memo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { getBalances } from './api';
import { AffiliateProgramOverview } from './Overview/components';
import WithdrawalForm from './WithdrawalForm';
import WithdrawalHistory from './WithdrawalHistory';
import { TabWrapper } from '../menu/tab-wrapper';
import { PageTitle } from '../styles';

export type AffiliateComponent = 'overview'|'payment-history'|'withdrawal';

const EMPTY_BALANCE = 0;

export const AffiliateProgram: FC = () => {
  const [componentToRender, setComponentToRender] = useState<AffiliateComponent>('overview');
  const [totalWithdrawnBalance, setTotalWithdrawnBalance] = useState<number>(EMPTY_BALANCE);
  const [totalEarnedBalance, setTotalEarnedBalance] = useState<number>(EMPTY_BALANCE);
  const [availableBalance, setAvailableBalance] = useState<number>(EMPTY_BALANCE);

  const { t: translation } = useTranslation();

  const updateComponent = (component: AffiliateComponent): void => {
    setComponentToRender(component);
  };

  useEffect(() => {
    requestBalances();
  }, [componentToRender]);

  const requestBalances = async (): Promise<void> => {
    const balances = await getBalances();
    const {
      totalWithdraw = EMPTY_BALANCE,
      totalEarned = EMPTY_BALANCE,
      available = EMPTY_BALANCE,
    } = balances || {};

    setTotalWithdrawnBalance(totalWithdraw);
    setTotalEarnedBalance(totalEarned);
    setAvailableBalance(available);
  };

  const renderContent = (): JSX.Element|null => {
    if (componentToRender === 'overview') {
      return (
        <AffiliateProgramOverview
          changeComponent={updateComponent}
          totalEarnedBalance={totalEarnedBalance}
          totalWithdrawnBalance={totalWithdrawnBalance}
          availableBalance={availableBalance}
        />
      );
    }

    if (componentToRender === 'payment-history') {
      return <WithdrawalHistory changeComponent={updateComponent} />;
    }

    if (componentToRender === 'withdrawal') {
      return (
        <WithdrawalForm
          changeComponent={updateComponent}
          availableBalance={availableBalance}
        />
      );
    }

    return null;
  };

  return (
    <TabWrapper tabKey='affiliate-program'>
      <PageTitle>
        {translation('personalArea.affiliateProgram.title')}
      </PageTitle>
      {renderContent()}
    </TabWrapper>
  );
};

export default memo(AffiliateProgram);
