import { ICreateWithdrawal } from './interfaces';
import { API_BASE_URL } from '../../../../common/constants/constants';
import { http } from '../../../services';

export const getLinks = async () => {
  const req = await http(`${API_BASE_URL}/user/affiliate-links`);
  return req.body;
}

export const createLink = async (prefix: string) => {
  const requestBody = JSON.stringify({
    prefix,
  });

  const req = await http(`${API_BASE_URL}/user/affiliate-links`, {
    method: 'POST',
    body: requestBody,
  });

  return req.body;
}

export const getWithdrawals = async () => {
  const req = await http(`${API_BASE_URL}/user/affiliate-links/withdrawal`);
  return req.body;
}

export const getBalances = async () => {
  const req = await http(`${API_BASE_URL}/user/balance`);
  return req.body;
}

export const createWithdrawal = async (withdrawalData: ICreateWithdrawal) => {
  const requestBody = JSON.stringify(withdrawalData);

  const req = await http(`${API_BASE_URL}/user/affiliate-links/withdrawal`, {
    method: 'POST',
    body: requestBody,
  });

  return req.body;
}
