import styled from '@emotion/styled';
import React, { FC, memo } from 'react';
import { useTranslation } from 'react-i18next';

import TableRow from './TableRow';

const Bg = styled.table`
  background: var(--F6F7F9-personal-area-affiliate-program-withdrawal-history);
  border-radius: 3px;
  padding: 15px;
  margin-top: 16px;
`;

const Th = styled.th`
  color: var(--000000-personal-area-affiliate-program-withdrawal-history);
  opacity: 0.8;
`;

const THead = styled.thead`
  border-bottom: 1px solid var(--E6EAF1-personal-area-affiliate-program-withdrawal-history);
`;

interface ITable {
  payments: any[];
}

const Table: FC<ITable> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Bg cellPadding={16}>
        <THead>
          <Th>#</Th>
          <Th>{translation('personalArea.affiliateProgram.date')}</Th>
          <Th>ID</Th>
          <Th>{translation('personalArea.affiliateProgram.type')}</Th>
          <Th>{translation('personalArea.affiliateProgram.status')}</Th>
          <Th>{translation('personalArea.affiliateProgram.total')}</Th>
        </THead>
        <tbody>
          {props.payments.map((
            (payment, index) => <TableRow key={payment.id} index={index} rowInfo={payment} />
          ))}
        </tbody>
      </Bg>
    </>
  );
};

export default memo(Table);

