import styled from '@emotion/styled';
import { Input } from 'antd';
import React, { FC, memo, useEffect, useState } from 'react';

import { E_WITHDRAWAL_METHODS, IWithdrawalMethod } from '../../interfaces/components/withdrawal-method.interface';

const Subtitle = styled.span`
  margin-top: 16px;
  color: var(--333333-personal-area-affiliate-program-withdrawal-form);
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 9px;
`;

interface IPayPalForm {
  onValidated: (isValid: boolean, method: IWithdrawalMethod) => any;
}
const CardNumberForm: FC<IPayPalForm> = (props) => {
  const [cardNumber, changeCardNumber] = useState<string>('');
  const [cardHolderName, changeCardHolderName] = useState<string>('');
  const [phoneNumber, changePhoneNumber] = useState<string>('');

  const validateForm = () => {
    const isFormValid = !!cardNumber;

    props.onValidated(isFormValid, {
      type: E_WITHDRAWAL_METHODS['card-number'],
      cardNumber,
      cardHolderName,
      phoneNumber,
      currency: 'USD',
    });
  };

  useEffect(() => {
    validateForm();
  }, [cardNumber, cardHolderName, phoneNumber]);

  const onCardNumberChange = (e: any) => {
    const { value } = e.target;

    changeCardNumber(value);
    validateForm();
  }

  const onCardHolderChange = (e: any) => {
    const { value } = e.target;

    changeCardHolderName(value);
    validateForm();
  }

  const onPhoneNumberChange = (e: any) => {
    const { value } = e.target;

    changePhoneNumber(value);
    validateForm();
  }

  return (
    <>
      <Subtitle>Card holder name</Subtitle>
      <Input value={cardHolderName} placeholder='John Smith' onChange={onCardHolderChange} />

      <Subtitle>Phone number</Subtitle>
      <Input value={phoneNumber} prefix='+' placeholder='1 (123) 456 78 90' onChange={onPhoneNumberChange} />

      <Subtitle>Card number</Subtitle>
      <Input value={cardNumber} placeholder='4242 4242 4242 4242' onChange={onCardNumberChange} />
    </>
  );
}

export default memo(CardNumberForm);
