import styled from '@emotion/styled';
import React, { FC, memo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import Table from './components/Table';
import IconArrowLeftPayment from '../../../../ui/icons/IconArrowLeftPayment';
import { getWithdrawals } from '../api';
import { AffiliateComponent } from '../index';

const PageTitle = styled('span')`
	margin-left: 16px;
	color: var(--000000-personal-area-affiliate-program-withdrawal-history);
	font-family: Roboto;
	font-size: 20px;
	line-height: 23px;
`;

const Col = styled('div')`
  display: flex;
  flex-direction: column;
  margin-top: 20px;
`;

const Row = styled('div')`
  display: flex;
  cursor: pointer;
  align-items: center;
  margin-bottom: 31px;
`;

interface IWithdrawalHistory {
  changeComponent: (component: AffiliateComponent) => any;
}
const WithdrawalHistory: FC<IWithdrawalHistory> = (props) => {
  const [withdrawals, changeWithdrawals] = useState<any[]>([]);

  const { t: translation } = useTranslation();

  const requestPayments = async () => {
    const withdrawalsList = await getWithdrawals();
    changeWithdrawals(withdrawalsList);
  };

  useEffect(() => {
    requestPayments();
  }, []);

  return (
    <>
      <Col>
        <Row onClick={() => props.changeComponent('overview')}>
          <IconArrowLeftPayment />
          <PageTitle>{translation('personalArea.billing.subTitlePaymentHistory')}</PageTitle>
        </Row>
        <Table payments={withdrawals} />
      </Col>
    </>
  );
};

export default memo(WithdrawalHistory);
