import styled from '@emotion/styled';
import React, { FC, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import IconRightArrowWithdrawal from '../../../../../ui/icons/IconRightArrowWithdrawal';
import { createLink, getLinks } from '../../api';
import BalanceCard from './BalanceCard';
import CreateForm from './CreateForm';
import Table from './Table';
import { AffiliateComponent } from '../../index';

const PageTitle = styled('h1')`
  margin: 0 !important;
  color: var(--000000-personal-area-affiliate-program);
  font-family: Roboto;
  font-size: 20px;
  line-height: 23px;
`;

const Col = styled('div')`
  display: flex;
  flex-direction: column;
`;

const Row = styled('div')`
  margin-top: 20px;
  display: flex;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--F0F0F0-personal-area-affiliate-program);
  flex-wrap: wrap;
`;

const WithdrawalBtn = styled.button`
  background-color: var(--13AB79-personal-area-affiliate-program);
  margin-left: 16px;
  user-select: none;
  border-radius: 3px;
  color: var(--FFFFFF-personal-area-affiliate-program);
  padding: 15px 20px;
  font-size: 14px;
  line-height: 16px;
  border: none;
  outline: none;
  cursor: pointer;
`;

const WithdrawHistoryBtnContainer = styled.div`
  margin-top: 16px;
`;

const GoToWithdrawHistoryBtn = styled.button`
  cursor: pointer;
  user-select: none;
  background: var(--F6F7F9-personal-area-affiliate-program);
  border-radius: 3px;
  padding: 15px;
  color: var(--333333-personal-area-affiliate-program);
  font-size: 14px;
  line-height: 16px;
  border: none;
  outline: none;
`;

const IntercomOpener = styled.span`
  cursor: pointer;
  color: var(--00A987-personal-area-affiliate-program);
`;

const IconContainer = styled.span`
  margin-left: 8px;
`;

interface IAffiliateProgramOverview {
  changeComponent: (component: AffiliateComponent) => any;
  totalWithdrawnBalance: number;
  totalEarnedBalance: number;
  availableBalance: number;
}
export const AffiliateProgramOverview: FC<IAffiliateProgramOverview> = (props) => {
  const [links, changeLinks] = useState<any[]>([]);

  const { t: translation } = useTranslation();

  useEffect(() => {
    requestLinks();
  }, []);

  const requestLinks = async () => {
    const linksList = await getLinks();
    const formattedLinksArray = linksList.map((link: any) => ({
      prefix: link.uri.split('-')[0],
      referrals: link.referralsCount || 0,
      total: link.total || 0,
      clicks: link.clicks || 0,
      id: link.id,
      uri: link.uri,
      paidUsersCount: link.paidUsersCount || 0,
      sumAmount: link.sumAmount || 0,
    }));

    changeLinks(formattedLinksArray);
  };

  const onCreateLink = async (prefix: string) => {
    try {
      const link = await createLink(prefix);
      changeLinks([
        {
          prefix: link.uri.split('-')[0],
          referrals: 0,
          total: 0,
          clicks: 0,
          id: link.id,
          uri: link.uri,
          paidUsersCount: 0,
          sumAmount: 0,
        },
        ...links,
      ]);
    } finally {
      return null;
    }
  };

  return (
    <>
      <Col>
        <Row>
          <div>
            <p style={{ maxWidth: 550 }}>{translation('personalArea.affiliateProgram.subtitle')}</p>
            <p>{translation('personalArea.affiliateProgram.subtitleSupport')}<IntercomOpener className='open_intercom'>{translation('base.here')}</IntercomOpener>.</p>
          </div>
        </Row>

        <Row>
          <BalanceCard
            title={translation('personalArea.affiliateProgram.cardTitleTotalWithdrawn')}
            priceInCents={props.totalWithdrawnBalance}
            width={182}
          />
          <BalanceCard
            title={translation('personalArea.affiliateProgram.cardTitleTotalEarned')}
            priceInCents={props.totalEarnedBalance}
          />
          <BalanceCard
            title={translation('personalArea.affiliateProgram.cardTitleAvailableForWithdrawing')}
            priceInCents={props.availableBalance}
            width={229}
          />
          <BalanceCard
            title={translation('personalArea.affiliateProgram.cardTitleYourAffiliate')}
            priceInCents={10}
            type='percent'
            width={199}
          />
        </Row>

        <Row>
          <CreateForm onCreate={onCreateLink} />
        </Row>

        <Table links={links} />

        <WithdrawHistoryBtnContainer>
          <GoToWithdrawHistoryBtn onClick={() => props.changeComponent('payment-history')}>
            <span>
              {translation('personalArea.affiliateProgram.paymentHistorySubmitBtnText')}
              <IconContainer>
                <IconRightArrowWithdrawal />
              </IconContainer>
            </span>
          </GoToWithdrawHistoryBtn>

          <WithdrawalBtn onClick={() => props.changeComponent('withdrawal')}>
            {translation('personalArea.affiliateProgram.withdrawalSubmitBtnText')}
          </WithdrawalBtn>
        </WithdrawHistoryBtnContainer>
      </Col>
    </>
  );
};
