import styled from '@emotion/styled';
import { Input } from 'antd';
import React, { ChangeEvent, FC, memo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { E_WITHDRAWAL_METHODS, IWithdrawalMethod } from '../../interfaces/components/withdrawal-method.interface';

const Subtitle = styled.span`
  margin-top: 16px;
  color: var(--333333-personal-area-affiliate-program-withdrawal-form);
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 9px;
`;

interface IUSDTForm {
  onValidated: (isValid: boolean, method: IWithdrawalMethod) => any;
}

const USDTForm: FC<IUSDTForm> = (props) => {
  const [address, setAddress] = useState<string>('');

  const { t: translation } = useTranslation();

  const onAddressChange = (event: ChangeEvent<HTMLInputElement>): void => {
    const { value } = event.target;

    const ADDRESS_REGEX = /^0x[0-9A-Fa-f]{40}$/;
    const isAddressValid = ADDRESS_REGEX.test(value);

    setAddress(value);
    props.onValidated(isAddressValid, {
      type: E_WITHDRAWAL_METHODS.usdt,
      currency: 'USD',
      usdtWalletAddress: value,
    });
  };

  return (
    <>
      <Subtitle>
        {translation('personalArea.affiliateProgram.withdraw.usdtWalletAddress')}
      </Subtitle>
      <Input type='text'
        value={address}
        placeholder={translation('personalArea.affiliateProgram.withdraw.usdtAddressPlaceholder') || ''}
        onChange={onAddressChange} />
    </>
  );
};

export default memo(USDTForm);
