import styled from '@emotion/styled';
import { Button, Checkbox, Input, Select } from 'antd';
import React, { FC, memo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import BankForm from './BankForm';
import CardNumberForm from './CardNumberForm';
import PayPalForm from './PayPalForm';
import USDTForm from './USDTForm';
import { ICreateWithdrawal } from '../../interfaces';
import { E_WITHDRAWAL_METHODS, IWithdrawalMethod, T_WITHDRAWAL_METHODS } from '../../interfaces/components/withdrawal-method.interface';

const Bg = styled.div`
  width: 100%;
  background: var(--F6F7F9-personal-area-affiliate-program-withdrawal-form);
  border-radius: 3px;
  padding: 12px 15px;
`;

const FormContainer = styled.div`
  width: 279px;
  display: flex;
  flex-direction: column;
`;

const Subtitle = styled.span`
  color: var(--333333-personal-area-affiliate-program-withdrawal-form);
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 9px;
`;

const AmountSubtitle = styled(Subtitle)`
  margin-top: 16px;
`;

const ChBoxContainer = styled.div`
  margin-top: 9px;
  color: var(--737373-personal-area-affiliate-program-withdrawal-form);
  font-size: 14px;
  line-height: 17px;
`;

const WithdrawBtnContainer = styled.div`
  margin-top: 24px;
  font-size: 14px;
  line-height: 16px;
  color: var(--4B4B4B-personal-area-affiliate-program-withdrawal-form);
  display: flex;
  align-items: center;
`;

const WillBeAbleTxt = styled.span`
  margin-left: 17px;
`;

const MINIMAL_WITHDRAWAL_AMOUNT = {
  [E_WITHDRAWAL_METHODS.paypal]: 1,
  [E_WITHDRAWAL_METHODS.bank]: 1,
  [E_WITHDRAWAL_METHODS['card-number']]: 1,
  [E_WITHDRAWAL_METHODS.usdt]: 1,
};

type TSelectedForm = T_WITHDRAWAL_METHODS;
interface IForm {
  maxWithdrawSumInCents: number;
  onWithdrawClicked: (withdrawal: ICreateWithdrawal) => any;
}

const Form: FC<IForm> = (props) => {
  const [selectedForm, changeSelectedForm] = useState<TSelectedForm>(E_WITHDRAWAL_METHODS.paypal);
  const [withdrawMethodNeedSave, changeWithdrawMethodNeedSave] = useState<boolean>(true);
  const [withdrawSum, changeWithdrawSum] = useState<number>(MINIMAL_WITHDRAWAL_AMOUNT[selectedForm]);
  const [isSelectedFormValid, changeSelectedFormValid] = useState<boolean>(false);
  const [createWithdrawBtnLoading, changeCreateWithdrawBtnLoading] = useState<boolean>(false);
  const [method, changeMethod] = useState<IWithdrawalMethod>();

  const { t: translation } = useTranslation();

  const onAmountChange = (e: any) => {
    const { value } = e.target;

    if (/^[^0-9]$/.test(value)) {
      return null;
    }

    const amount: number = +value;

    if (isNaN(amount)) {
      return null;
    }

    if (amount > (props.maxWithdrawSumInCents / 100 | 0)) {
      changeWithdrawSum(props.maxWithdrawSumInCents / 100 | 0);

      return null;
    }

    if (amount < MINIMAL_WITHDRAWAL_AMOUNT[selectedForm]) {
      changeWithdrawSum(MINIMAL_WITHDRAWAL_AMOUNT[selectedForm]);

      return null;
    }

    changeWithdrawSum(amount);
  };

  const selectForm = (v: TSelectedForm) => {
    changeWithdrawSum(MINIMAL_WITHDRAWAL_AMOUNT[v]);
    changeSelectedForm(v);
  };

  const onFormValidate = (isValid: boolean, method: IWithdrawalMethod) => {
    changeSelectedFormValid(isValid);
    changeMethod(method);
  };

  const createWithdraw = () => {
    try {
      changeCreateWithdrawBtnLoading(true);
      if (method) {
        props.onWithdrawClicked({
          amount: withdrawSum * 100,
          method,
        });
      }
    } catch {
      changeCreateWithdrawBtnLoading(false);
    } finally {
      return null;
    }
  };

  return (
    <Bg>
      <FormContainer>
        <Subtitle>{translation('personalArea.affiliateProgram.withdraw.to')}</Subtitle>
        <Select value={selectedForm} onChange={selectForm}>
          <Select.Option value={E_WITHDRAWAL_METHODS.paypal}>
            PayPal
          </Select.Option>
          <Select.Option value={E_WITHDRAWAL_METHODS.bank}>
            {translation('personalArea.affiliateProgram.withdraw.wire')}
          </Select.Option>
          <Select.Option value={E_WITHDRAWAL_METHODS['card-number']}>
            {translation('personalArea.affiliateProgram.withdraw.cardNumber')}
          </Select.Option>
          <Select.Option value={E_WITHDRAWAL_METHODS.usdt}>
            USDT BEP-20
          </Select.Option>
        </Select>

        {selectedForm === E_WITHDRAWAL_METHODS.paypal ? <PayPalForm onValidated={onFormValidate} /> : null}
        {selectedForm === E_WITHDRAWAL_METHODS.bank ? <BankForm onValidated={onFormValidate} /> : null}
        {selectedForm === E_WITHDRAWAL_METHODS['card-number'] ? <CardNumberForm onValidated={onFormValidate} /> : null}
        {selectedForm === E_WITHDRAWAL_METHODS.usdt ? <USDTForm onValidated={onFormValidate} /> : null}

        <AmountSubtitle>{translation('personalArea.affiliateProgram.withdraw.amount')}</AmountSubtitle>
        <Input
          suffix={`${translation('personalArea.affiliateProgram.withdraw.available')} ${props.maxWithdrawSumInCents ? '$' + (props.maxWithdrawSumInCents / 100 | 0)  : 0}`}
          prefix='$'
          disabled={(props.maxWithdrawSumInCents / 100 | 0) < MINIMAL_WITHDRAWAL_AMOUNT[selectedForm]}
          value={withdrawSum}
          onChange={onAmountChange}
        />

        <ChBoxContainer>
          <Checkbox
            checked={withdrawMethodNeedSave}
            onChange={(e => changeWithdrawMethodNeedSave(e.target.checked))}
          >
            {translation('personalArea.affiliateProgram.withdraw.saveBtn')}
          </Checkbox>
        </ChBoxContainer>

        <WithdrawBtnContainer>
          <Button
            disabled={
              //    
              !isSelectedFormValid
              //      
              || withdrawSum < MINIMAL_WITHDRAWAL_AMOUNT[selectedForm]
              //    
              || withdrawSum > (props.maxWithdrawSumInCents / 100 | 0)
            }
            loading={createWithdrawBtnLoading}
            onClick={createWithdraw}
          >
            {translation('personalArea.affiliateProgram.withdraw.withdrawal')}
          </Button>
          <WillBeAbleTxt>{translation('personalArea.affiliateProgram.withdraw.availableIn')}</WillBeAbleTxt>
        </WithdrawBtnContainer>
      </FormContainer>
    </Bg>
  );
};

export default memo(Form);
