import styled from '@emotion/styled';
import React, { FC, memo } from 'react';
import { useTranslation } from 'react-i18next';

import TableRow from './TableRow';

const Bg = styled.table`
  background: var(--F6F7F9-personal-area-affiliate-program-table);
  border-radius: 3px;
  padding: 15px;
  margin-top: 16px;
`;

const ThRightHanded = styled.th`
  color: var(--000000-personal-area-affiliate-program-table);
  opacity: 0.8;
  text-align: right;
`;

const ThLeftHanded = styled.th`
  color: var(--000000-personal-area-affiliate-program-table);
  opacity: 0.8;
`;

const THead = styled.thead`
  border-bottom: 1px solid var(--E6EAF1-personal-area-affiliate-program-table);
`;

interface ITable {
  links: any[];
}

const Table: FC<ITable> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Bg cellPadding={16}>
        <THead>
          <ThLeftHanded>#</ThLeftHanded>
          <ThLeftHanded>{translation('personalArea.affiliateProgram.link')}</ThLeftHanded>
          {/*<Th>Note</Th>*/}
          <ThRightHanded>{translation('personalArea.affiliateProgram.clicks')}</ThRightHanded>
          <ThRightHanded>{translation('personalArea.affiliateProgram.referrals')}</ThRightHanded>
          {/*<Th>Total</Th>*/}
          <ThRightHanded>{translation('personalArea.affiliateProgram.paidUsersCount')}</ThRightHanded>
          <ThRightHanded>{translation('personalArea.affiliateProgram.sumAmount')}</ThRightHanded>
        </THead>
        <tbody>
          {props.links.map(((link, index) => <TableRow key={link.id} index={index} rowInfo={link} onNoteChange={() => null}/>))}
        </tbody>
      </Bg>
    </>
  );
};

export default memo(Table);
