import React, { FC, memo, useState } from 'react';
import styled from '@emotion/styled';
import { Button } from 'antd';
import { useTranslation } from 'react-i18next';

const Bg = styled.div`
  display: flex;
  margin-top: 16px;
  background: var(--F6F7F9-personal-area-affiliate-program-create-form);
  border-radius: 3px;
  flex-direction: column;
  padding: 16px;
`;

const InputContainer = styled.div`
  background: var(--FFFFFF-personal-area-affiliate-program-create-form);
  border: 1px solid var(--E1E2E5-personal-area-affiliate-program-create-form);
  border-radius: 3px;
  display: flex;
  align-items: center;
  padding: 0 0 0 12px;
`;

const InputText = styled.span`
  color: var(--434343-personal-area-affiliate-program-create-form);
  font-size: 14px;
`;

const PrefixInput = styled.input`
  padding: 3px 8px;
  margin: 0 10px;
  border: none;
  border-left: 1px solid var(--BCBFC9-personal-area-affiliate-program-create-form);
  //border-right: 1px solid var(--BCBFC9-personal-area-affiliate-program-create-form);
  font-size: 14px;
  outline: none;
  background: var(--FFFFFF-personal-area-affiliate-program-create-form);
`;

const Row = styled.div`
  margin-top: 9px;
  display: flex;
  justify-content: space-between;
`;

const Title = styled.span`
  color: var(--333333-personal-area-affiliate-program-create-form);
  font-size: 14px;
  line-height: 16px;
  display: inline-block;
`;

const CreateBtn = styled(Button)`
  margin-left: 16px;
`;

interface ICreateForm {
  onCreate: (prefix: string) => any;
}
const CreateForm: FC<ICreateForm> = (props) => {
  const [prefix, changePrefix] = useState<string>('');
  const [createBtnLoading, changeCreateBtnLoading] = useState<boolean>(false);

  const { t: translation } = useTranslation();

  const onPrefixChange = (e: any) => {
    const { value } = e.target;

    if (!/^[a-z]*$/i.test(value)) {
      return null;
    }

    changePrefix(value);
  }

  const onCreateBtnClick = async () => {
    try {
      changeCreateBtnLoading(true);
      await props.onCreate(prefix);
    } finally {
      changeCreateBtnLoading(false);
    }
  }

  return (
    <>
      <Bg>
        <Title>
          {translation('personalArea.affiliateProgram.inputTitleStoreSubdomain')}
        </Title>
        <Row>
          <InputContainer>
            <InputText>
              https://go.gologin.com/
            </InputText>
            <PrefixInput
              placeholder={translation('personalArea.affiliateProgram.placeholderInputYourPrefix')}
              value={prefix}
              onChange={onPrefixChange}
            />
            {/*<InputText>random</InputText>*/}
          </InputContainer>
          <CreateBtn
            onClick={onCreateBtnClick}
            loading={createBtnLoading}
          >
            {translation('personalArea.affiliateProgram.createReferralLinkSubmitBtnText')}
          </CreateBtn>
        </Row>
      </Bg>
    </>
  )
}

export default memo(CreateForm);
