import styled from '@emotion/styled';
import { Badge, Tooltip } from 'antd';
import moment from 'moment';
import React, { FC, memo } from 'react';
import { useTranslation } from 'react-i18next';

const IdTd = styled.td`
  max-width: 100px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
`;

interface ITableRow {
  rowInfo: any;
  index: number;
  onNoteChange?: (index: number, note: string) => any;
}

const TableRow: FC<ITableRow> = (props) => {
  const { t: translation } = useTranslation();

  type AffiliateStatus = 'processing'|'completed'|'rejected';

  const renderStatuses: Record<AffiliateStatus, JSX.Element> = {
    processing: (
      <Badge status='processing' text='In Process' />
    ),
    completed: (
      <Badge status='success' text={translation('base.paid')} />
    ),
    rejected: (
      <Badge status='error' text='Canceled' />
    ),
  };

  const renderStatus = (status: AffiliateStatus): JSX.Element => renderStatuses[status];

  return (
    <>
      <tr>
        <td>
          {props.index + 1}
        </td>
        <td>
          {props.rowInfo.createdAt ? moment(props.rowInfo.createdAt).format('DD.MM.YY') : '-'}
        </td>
        <IdTd>
          <Tooltip title={props.rowInfo.id || '-'}>
            {props.rowInfo.id || '-'}
          </Tooltip>
        </IdTd>
        <td>
          {props.rowInfo.type}
        </td>
        <td>
          {renderStatus(props.rowInfo.status)}
        </td>
        <td>
          {Intl.NumberFormat('en-US', {
            style: 'currency',
            currency: 'USD',
            minimumFractionDigits: 0,
            maximumFractionDigits: 0,
          }).format(Math.abs(props.rowInfo.amount) / 100 | 0)}
        </td>
      </tr>
    </>
  );
};

export default memo(TableRow);
