import styled from '@emotion/styled';
import { Input, Select } from 'antd';
import React, { FC, memo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { E_WITHDRAWAL_METHODS, IWithdrawalMethod } from '../../interfaces/components/withdrawal-method.interface';

const Subtitle = styled.span`
  margin-top: 16px;
  color: var(--333333-personal-area-affiliate-program-withdrawal-form);
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 9px;
`;

type TAccountType = 'c'|'s';
interface IBankForm {
  onValidated: (isValid: boolean, method: IWithdrawalMethod) => any;
}

const BankForm: FC<IBankForm> = (props) => {
  const [bankName, setBankName] = useState<string>('');
  const [accountHolderName, setAccountHolderName] = useState<string>('');
  const [accountNumber, setAccountNumber] = useState<string>('');
  const [routingNumber, setRoutingNumber] = useState<string>('');
  const [accountType, setAccountType] = useState<TAccountType>('c');

  const { t: translation } = useTranslation();

  const validateForm = () => {
    const bName = bankName.replace(/[^a-z]ig/, '');
    const aHolderName = accountHolderName.replace(/[^a-z]ig/, '');

    const isFormValid = !!bName && !!aHolderName && !!(+accountNumber) && !!(+routingNumber);
    props.onValidated(isFormValid, {
      type: E_WITHDRAWAL_METHODS.bank,
      bankName,
      bankAccountType: accountType,
      bankRoutingNumber: routingNumber,
      bankAccountNumber: accountNumber,
      bankUsername: accountHolderName,
      currency: 'USD',
    });
  };

  useEffect(() => {
    validateForm();
  }, [bankName, accountNumber, accountHolderName, routingNumber, accountType]);

  const onChangeBankName = (e: any) => {
    const { value } = e.target;

    setBankName(value);
    validateForm();
  };

  const onChangeAccountHolderName = (e: any) => {
    const { value } = e.target;

    setAccountHolderName(value);
    validateForm();
  };

  const onChangeAccountNumber = (e: any) => {
    const { value } = e.target;

    setAccountNumber(value);
    validateForm();
  };

  const onChangeRoutingNumber = (e: any) => {
    const { value } = e.target;

    setRoutingNumber(value);
    validateForm();
  };

  const onChangeAccountType = (v: TAccountType) => {
    setAccountType(v);
    validateForm();
  };

  return (
    <>
      <Subtitle>{translation('personalArea.affiliateProgram.withdraw.bankName')}</Subtitle>
      <Input
        type='text'
        value={bankName}
        placeholder='Bank Of America'
        onChange={onChangeBankName}
      />

      <Subtitle>{translation('personalArea.affiliateProgram.withdraw.accountHolderName')}</Subtitle>
      <Input
        type='text'
        value={accountHolderName}
        placeholder='John Smith'
        onChange={onChangeAccountHolderName}
      />

      <Subtitle>{translation('personalArea.affiliateProgram.withdraw.accountNumber')}</Subtitle>
      <Input
        type='text'
        value={accountNumber}
        placeholder='8276019671'
        onChange={onChangeAccountNumber}
      />

      <Subtitle>{translation('personalArea.affiliateProgram.withdraw.routingNumber')}</Subtitle>
      <Input
        type='text'
        value={routingNumber}
        placeholder='122105155'
        onChange={onChangeRoutingNumber}
      />

      <Subtitle>{translation('personalArea.affiliateProgram.withdraw.accountType')}</Subtitle>
      <Select value={accountType} onChange={onChangeAccountType}>
        <Select.Option value='c'>{translation('personalArea.affiliateProgram.withdraw.accountTypeChecking')}</Select.Option>
        <Select.Option value='s'>{translation('personalArea.affiliateProgram.withdraw.accountTypeSaving')}</Select.Option>
      </Select>
    </>
  );
};

export default memo(BankForm);
